import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { AlertHelisaType } from './alert-helisa-type.enum';

interface AlertHelisaProperties {
  title: string;
  content: string;
  type: AlertHelisaType;
}

@Component({
  selector: 'hel-alert',
  templateUrl: './alert-helisa.component.html',
  styleUrls: ['./alert-helisa.component.sass']
})
export class AlertHelisaComponent implements OnInit {

  content: string;
  title: string;
  hasCancel: boolean;

  constructor(
    public dialogRef: MatDialogRef<AlertHelisaComponent>,
    @Inject(MAT_DIALOG_DATA) public data: AlertHelisaProperties
  ) {
    this.content = data.content;
    this.title = data.title;
    this.hasCancel = data.type === AlertHelisaType.CONFIRMATION;
    dialogRef.disableClose = true;
    dialogRef.keydownEvents().subscribe((event: KeyboardEvent) => {
      if (event.code === 'Escape') {
        this.dialogRef.close(this.onCancel());
      }
    });
  }

  ngOnInit(): void {
  }

  onCancel(): void {
    this.dialogRef.close();
  }
}
