import { Directive, HostBinding, PLATFORM_ID, Inject, Input, OnChanges } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';

@Directive({
  selector: 'a[href]',
})
export class ExternalLinkDirective implements OnChanges {
  @HostBinding('attr.rel') relAttr: string = '';
  @HostBinding('attr.target') targetAttr: string = '';
  @HostBinding('attr.href') hrefAttr: string = '';
  @Input() href: string;

  constructor(@Inject(PLATFORM_ID) private platformId: string) {}

  ngOnChanges(): void {

    this.hrefAttr = this.href;

    if (this.isLinkExternal()) {
      this.relAttr = 'noopener';
      this.targetAttr = '_blank';
    }
  }

  private isLinkExternal(): boolean {
    return isPlatformBrowser(this.platformId) && !this.href.includes(location.hostname);
  }
}
