import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'hel-input-with-button',
  templateUrl: './input-with-button.component.html',
  styleUrls: ['./input-with-button.component.sass']
})
export class InputWithButtonComponent implements OnInit {

  @Input() placeholder: string = '';
  @Input() inputFormControl: FormControl = new FormControl('', Validators.required);
  @Input() requiredMessage: string = 'El campo es requerido';
  @Input() value: string = '';
  @Input() isFocused: boolean = false;
  @ViewChild('inputText') nameField: ElementRef;

  @Output() done: EventEmitter<string> = new EventEmitter<string>();
  @Output() cancel: EventEmitter<void> = new EventEmitter<void>();


  constructor() { }

  ngOnInit(): void {
    if (this.value !== '') {
      this.inputFormControl.setValue(this.value);
    }
    this.nameField.nativeElement.focus();
  }


  onDone(): void {
    if (this.inputFormControl.valid) {
      this.done.emit(this.inputFormControl.value);
    }
  }

  onCancel(): void {
    this.cancel.emit();
  }

}
