import {SortDirection} from '@angular/material';
import { RowData } from './table-helisa.component';

export enum ColumnType {
  NORMAL, URL
}

export interface ColumnConfig {
  name: string;
  title?: string;
  visible?: boolean;
  sortable?: boolean;
  groupable?: boolean;
  totalType?: TotalType;
  searchable?: boolean;
  sortDirection?: SortDirection;
  subtitle?: string;
  colspanTitle?: number;
  colspanSubtitle?: number;
  columnStyle?: string;
  columnType?: ColumnType;
}

export interface AddRowButton {
  text: string;
  showButton: boolean;
}

export enum EventScope {
  USER, CODE_CALL
}

export enum TotalType {
  SUM, AVERAGE, COUNT
}

export enum ChangeColumnConfigurationType {
  SORT, UNKNOWN, TOTAL
}

export interface EventColumn {
  column: ColumnConfig;
  columnConfigurations: Array<ColumnConfig>;
  type: ChangeColumnConfigurationType;
}

export interface TotalTableHelisa {
  column: ColumnConfig;
  value: number;
}

export interface TotalGroup {
  sum: number;
  count: number;
}

export interface EventSearch {
  text: string;
  columnConfigurations: Array<ColumnConfig>;
}

export interface RequestTableHelisa<T> {
  page: number;
  body: {} | T;
}

export interface DropElement<T> {
 value: T;
 order: number;
}

export interface SelectObject<T> {
  value: T;
  scope: EventScope;
}

export enum TableHelisaType {
  REMOTE, LOCAL
}


export interface Cell<T> {
  column: ColumnConfig;
  row: RowData<T> | T;
 }

export interface ConfigCellStyles<T> {
   cellData: T;
   classCell: string;
 }

export interface ConfigRowStyles<T> {
  column: ColumnConfig;
  data: {} | T;
  classRow: string;
}

// @dynamic
export class ColumnConfigUtil<T> {

  getValue(obj: T, column: ColumnConfig): T | number | string {
    return column.name.split('.').reduce((o: T, field: string) => o && o[field], obj);
  }
}

