import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { Observable } from 'rxjs';
import { AlertHelisaType } from './alert-helisa-type.enum';
import { AlertHelisaComponent } from './alert-helisa.component';



@Injectable({
  providedIn: 'root'
})
export class AlertHelisaService {

  constructor(public dialog: MatDialog) { }

  openDialog(type: AlertHelisaType, title: string, content: string): Observable<boolean> {
    const dialogRef: MatDialogRef<AlertHelisaComponent> = this.dialog.open(AlertHelisaComponent, {
      width: '250px',
      data: { title, content, type }
    });

    return dialogRef.afterClosed();
  }
}
