import { Component, OnInit, Input } from '@angular/core';
import { FormControl } from '@angular/forms';

import * as moment_ from 'moment';
const moment: typeof moment_ = moment_;

import { MatDatepickerInputEvent, MatDatepicker } from '@angular/material';
import { filter, tap } from 'rxjs/operators';


export enum TypeCalendarEnum {
  NORMAL = 'norma',
  MONTH_YEAR = 'mounth-year'
}


@Component({
  selector: 'hel-date-helisa',
  templateUrl: './date-helisa.component.html',
  styleUrls: ['./date-helisa.component.css'],
})
export class DateHelisaComponent implements OnInit {


  @Input() floatLabel: 'never' | 'always' | 'auto' = 'never';
  @Input() dateFormControl: FormControl = new FormControl('');
  private date: Date = new Date();

  /**
   * Formato de fecha.
   * Los formatos validos son aquellos que maneja la libreria momentjs
   * https://momentjs.com/docs/#/parsing/string-format/
   */
  @Input() dateFormat: string = 'DD/MM/YYYY';
  @Input() errorMessage: string = 'La fecha no concuerda con el formato ';
  @Input() placeholder: string = this.dateFormat;

  /**
   * Si este valor es diferente a TypeCalendarEnum.NORMAL no
   * serÃ¡ tomado en cuenta
   */
  @Input() typeCalendar: TypeCalendarEnum = TypeCalendarEnum.NORMAL;


  dateToVisualize: FormControl;

  /**
   * Para evitar nuevos eventos miestras se realiza el parseo
   */
  private isFromInputEvent: boolean = false;

  /**
   * Verificar si el formato es valido
   */
  invalidFormat: boolean = false;

  constructor() { }

  ngOnInit(): void {
    this.dateToVisualize = new FormControl('', this.dateFormControl.validator);
    this.formHandler();

    if (this.typeCalendar === TypeCalendarEnum.MONTH_YEAR) {
      this.dateFormat = 'MM/YYYY';
      this.placeholder = this.dateFormat;
    }

    /**
     * establecer valor por defecto de la fecha
     */
    const incommingDate: string = moment(this.dateFormControl.value, this.dateFormat).format(this.dateFormat);
    if (this.dateFormControl.value !== '' && incommingDate !== 'Invalid date') {
      this.dateToVisualize.setValue(incommingDate);
    }

  }

  get typeCalendarEnum(): typeof TypeCalendarEnum {
    return TypeCalendarEnum;
  }

  /**
   * Determina como se debe inicializar la visualizacion del calendar
   */
  getStartView(): string {
    // multi-year
    if (this.typeCalendar === this.typeCalendarEnum.MONTH_YEAR) {
      return 'multi-year';
    } else {
      return 'month';
    }
  }

  private formHandler(): void {
    this.dateToVisualize.valueChanges
      .pipe(
        tap((date: string) => {
          if (date.length > this.dateFormat.length) {
            this.invalidFormat = true;
          } else {
            this.invalidFormat = false;
          }
        }),
        filter((date: string) => date.length === this.dateFormat.length)
      )
      .subscribe((date: string) => {
        this.invalidFormat = false;
        const isValid: boolean = moment(date, this.dateFormat, true).isValid();
        const result: string = moment(date, this.dateFormat).format('YYYY-MM-DD');

        if (!!result && (result === 'Invalid date' || !isValid)) {
          this.invalidFormat = true;
          return;
        }

        if (!!result) {
          if (!this.isFromInputEvent) {
            this.isFromInputEvent = true;
            const subString: string[] = result.split('-');

            const year: number = parseFloat(subString[0]);
            const month: number = parseFloat(subString[1]);
            const day: number = parseFloat(subString[2]);

            this.date.setFullYear(year);
            this.date.setDate(day);
            this.date.setMonth(month - 1); // -1 por que los meses se toman como los indices en un array

            /** cuando es de tipo MOUNTH_YEAR retorna el ultimo dia del mes seleccionado */
            if (this.typeCalendar === TypeCalendarEnum.MONTH_YEAR) {
              this.date = moment(this.date).endOf('month').toDate();
            }

            this.dateToVisualize.setValue(moment(this.date, 'YYYY-MM-DD').format(this.dateFormat));
            this.dateFormControl.setValue(this.date);

            this.isFromInputEvent = false;
          } else {
            setTimeout(() => {
              this.isFromInputEvent = false;
            }, 1500);
          }

        }
      });


    this.dateFormControl.valueChanges
      .subscribe((date: string) => {
        const incommingDate: string = moment(date, this.dateFormat).format(this.dateFormat);
        if (this.dateFormControl.value !== '' && incommingDate !== 'Invalid date') {
          this.dateToVisualize.setValue(incommingDate);
        }
      });
  }


  /**
   * Evento que se dispara luego seleccionar un mes
   */
  monthSelectedHandler(chosenMonthDate: moment_.Moment, datepicker: MatDatepicker<moment_.Moment>): void {

    if (this.typeCalendar === TypeCalendarEnum.MONTH_YEAR) {
      datepicker.close();
      const date: Date = moment(chosenMonthDate).endOf('month').toDate();

      this.dateToVisualize.setValue(moment(date, 'YYYY-MM-DD').format(this.dateFormat));
      this.dateFormControl.setValue(date);
    }
  }

  /**
   * Evento desde el control touch del calendar
   */
  dateChange(type: string, event: MatDatepickerInputEvent<Date>): void {

    this.dateToVisualize.setValue(moment(event.value, 'YYYY-MM-DD').format(this.dateFormat));
    this.dateFormControl.setValue(event.value);
  }

  getErrorMessage(): string {
    return this.errorMessage + this.dateFormat;
  }

}
