import { Directive, Input, HostListener, ElementRef } from '@angular/core';
import { MatTooltip } from '@angular/material';

@Directive({
    selector: '[helTooltip]',
    providers: [MatTooltip]
})
export class HelTooltipDirective {

    tooltip: MatTooltip;

    /**
     * Mensaje a mostrar
     */
    @Input('helTooltip') message: string;

    /**
     * Tiempo antes de ocultarla el mensaje
     */
    @Input() hideDelay: number = 600;

    /**
     * Tiempo antes de mostra el mensaje
     */
    @Input() showDelay: number = 500;


    constructor(tooltip: MatTooltip, private elemRef: ElementRef) {
      this.tooltip = tooltip;
    }

    @HostListener('mouseover') mouseover(): void {
      const currentContent: string = this.elemRef.nativeElement.innerText;

      if (!!currentContent && !!this.message) {
        if ((currentContent.toUpperCase() !== this.message.toString().toUpperCase()) || this.isEllipsisActive(this.elemRef.nativeElement)) {
          this.tooltip.message = this.message;
        }
      }

      this.tooltip.showDelay =  this.showDelay;
      this.tooltip.hideDelay = this.hideDelay;
    }


    private isEllipsisActive(e: {offsetWidth: number , scrollWidth: number}): boolean {
      return (e.offsetWidth < e.scrollWidth);
    }
}
