import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable, of, Subject } from 'rxjs';
import { map, startWith, throttleTime, debounceTime } from 'rxjs/operators';
import { AutocompleteHelisaService } from './autocomplete-helisa.component.service';

export interface OptionAutocompleteHelisa<T> {
  value: T;
  displayText: string;
}

@Component({
  selector: 'hel-autocomplete',
  templateUrl: './autocomplete-helisa.component.html',
  styleUrls: ['./autocomplete-helisa.component.css'],
  providers: [AutocompleteHelisaService]
})
export class AutocompleteHelisaComponent<T> implements OnInit {

  @Input() myControl: FormControl = new FormControl();
  @Input() options: Array<OptionAutocompleteHelisa<T>> = new Array<OptionAutocompleteHelisa<T>>();
  filteredOptions: Observable<OptionAutocompleteHelisa<T>[]>;
  selectedValue: OptionAutocompleteHelisa<T>;
  @Output() selectedValueEmmiter: EventEmitter<T> = new EventEmitter<T>();
  @Output() nextPage: EventEmitter<void> = new EventEmitter<void>();
  @Input() isRemote: boolean = false;
  isLoading: boolean = false;


  onScrollObservable: Subject<void> = new Subject<void>();

  constructor(private autocompleteHelisaService: AutocompleteHelisaService<T>) {
  }

  ngOnInit(): void {

    this.onScrollObservable.asObservable()
    .pipe(
      debounceTime(500),
      throttleTime(500)
    )
    .subscribe(() => {
      this.nextPage.emit();
    });

    if (this.isRemote) {
      this.autocompleteHelisaService.dataSource$.subscribe((data: OptionAutocompleteHelisa<T>[]) => {
        setTimeout(() => {
          this.options = data;
          this.filteredOptions = of<OptionAutocompleteHelisa<T>[]>(this.options);
        });
      });
    }

    this.filteredOptions = this.myControl.valueChanges.pipe(
      startWith(''),
      map((x: string) => this._checkRegex(x)),
      map((value: string) => this._filter(value))
    );

  }

  displayFn(option?: OptionAutocompleteHelisa<T>): string | undefined {
    return option ? option.displayText : undefined;
  }

  getService(): AutocompleteHelisaService<T> {
    return this.autocompleteHelisaService;
  }


  /** Elimina caracteres extraÃ±os */
  private _checkRegex(value: string): string {
    value = value.replace(/[-\/\\^$*+?.()|[\]{}]/g, '');
    return value;
  }

  private _filter(value: string): OptionAutocompleteHelisa<T>[] {
    if (!(value)) {
      if (!this.isRemote) {
        const filterValue: string[] = value.toLowerCase().split(' ');
        return this.options.filter((option: OptionAutocompleteHelisa<T>) => {
          let ws: boolean = true;
          filterValue.forEach((text: string) => ws = ws && option.displayText.toLowerCase().indexOf(text) >= 0);
          return ws;
        }).splice(0, 5);
      } else {
        return this.options;
      }
    }
  }

  public onSelected(event: {option: {value: OptionAutocompleteHelisa<T>}}): void {
    this.selectedValue = event.option.value;
    this.selectedValueEmmiter.emit(this.selectedValue.value);
  }

  getNextPage(): void {
    this.onScrollObservable.next();
  }
}
