import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { OptionAutocompleteHelisa } from './autocomplete-helisa.component';

@Injectable()
export class AutocompleteHelisaService<T> {

  private emitChangeSource: BehaviorSubject<Array<OptionAutocompleteHelisa<T>>> =
  new BehaviorSubject<Array<OptionAutocompleteHelisa<T>>>([]);

  public dataSource$: Observable<Array<OptionAutocompleteHelisa<T>>> = this.emitChangeSource.asObservable();

  constructor() { }

  setDataSource(options: Array<OptionAutocompleteHelisa<T>>): void {
    this.emitChangeSource.next(options);
  }

}
