import {ColumnConfig} from './table-helisa.interface';
import {Observable} from 'rxjs';


export class TableHelisaConnectComponent<T> {
  page: number;
  isLastPage: boolean;
  isUsed: boolean;

  constructor() {
    this.page = 0;
    this.isLastPage = false;
    this.isUsed = false;
  }

  public getBody(columnConfig: Array<ColumnConfig>, search: string): {} {
    return {};
  }

  nextPage(): number {
    return this.page++;
  }
}
