import { Component, OnInit, Input, Output, EventEmitter, ViewChild, AfterViewInit, ElementRef, HostListener } from '@angular/core';
import { NestedTreeControl, TreeControl } from '@angular/cdk/tree';
import { MatTreeNestedDataSource, MatTree, MatOptionSelectionChange } from '@angular/material';
import { Node } from './node';
import { TreeHelisaService } from './tree-helisa.service';
import { Router } from '@angular/router';
import * as _ from 'lodash';
import { TreeHelisaConnect } from './tree-helisa-connect';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';

export interface RequestTreeHelisa {
  page: number;
}

@Component({
  selector: 'hel-tree',
  templateUrl: './tree-helisa.component.html',
  styleUrls: ['./tree-helisa.component.sass']
  // host: { '(document:keyup)': 'onKeyDown($event)' }
})
export class TreeHelisaComponent implements OnInit, AfterViewInit {
  isDisabled: boolean;
  selectedNode: number | string | null;
  //#endregion ====== Variables ========

  constructor(private treeHelisaService: TreeHelisaService, private router: Router, private elementRef: ElementRef) {
    // cargar datos pasados por el @Input
    if (!!this.data) {
      const data: Node = this.data;
      this.data = null;
      this.receivePage(data.children);
    } else {
      this.dataSource.data = [];
      this.treeControl.dataNodes = [];
    }
  }

  //#region  ====== Variables =============
  private treeHelisaConnect: TreeHelisaConnect<Node>;
  formEdit: FormControl;
  @ViewChild('tree') tree: MatTree<{}>;
  private selectedOptions: Map<
    string | number,
    {
      formControl: FormControl;
      editMode: boolean;
    }
  > = new Map<
    string | number,
    {
      formControl: FormControl;
      editMode: boolean;
    }
  >();
  /**
   * Datos del Arbol
   */
  @Input() data: Node;

  /**
   * Retorna el id del nodo removido
   */
  @Output() removed: EventEmitter<string | number> = new EventEmitter<number | string>();

  /**
   * Retorna un nodo editado
   */
  @Output() edited: EventEmitter<Node> = new EventEmitter<Node>();

  /**
   * Retorna un nodo sin id del nodo , pero si con el parent
   * para conocer a cual fue aÃ±adido
   */
  @Output() added: EventEmitter<Node> = new EventEmitter<Node>();
  @Output() collapseParent: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() rangeScrolled: EventEmitter<RequestTreeHelisa> = new EventEmitter<RequestTreeHelisa>();
  @Output() nodeSelected: EventEmitter<number | string> = new EventEmitter<number | string>();
  @Output() dobleClick: EventEmitter<number | string> = new EventEmitter<number | string>();
  @Output() keypressDelete: EventEmitter<number | string | null> = new EventEmitter<number | string | null>();
  @Output() keypressInsert: EventEmitter<number | string | null> = new EventEmitter<number | string | null>();
  @Output() checkedOptionNode: EventEmitter<number | string | null> = new EventEmitter<number | string | null>();
  @Output() uncheckedOptionNode: EventEmitter<number | string | null> = new EventEmitter<number | string | null>();

  treeControl: NestedTreeControl<Node> = new NestedTreeControl<Node>((node: Node): Node[] | Observable<Node[]> => node.children);
  dataSource: MatTreeNestedDataSource<Node> = new MatTreeNestedDataSource<Node>();

  isSingleClick: boolean = true;
  currentNode: Node = null;

  /**
   * Obtiene la descripcion completa del nodo
   * @example Nodo padre,nodo hijo,nodo nieto
   * @param node Debe tener todos los parent llenos hacia arriba
   */
  public static getDescription(node: Node): string {
    let result: string[] = [node.name];
    let concat: string = '';

    if (node.parent) {
      result.push(this.getDescription(node.parent));
    }

    if (result.length === 1) {
      return node.name;
    }

    result = result.reverse();

    for (let i: number = 0; i < result.length; i++) {
      const element: string = result[i];
      concat = concat + element + (i === result.length - 1 ? '' : ',');
    }

    return concat;
  }

  ngOnInit(): void {
    // si se cargan datos por medio del servicio
    this.treeHelisaService.dataSourceObservable.subscribe((res: Node) => {
      if (!!res && !!res.children) {
        this.selectedNode = res.id;
        this.receivePage(res.children);
      } else {
        this.dataSource.data = [];
        this.treeControl.dataNodes = [];
      }
    });

    // Observable, si cambia el nodo seleccionado por medio del servicio
    this.treeHelisaService.nodeSelected.subscribe((res: string | number) => {
      if (!!this.data && !!this.data.children) {
        this.selectNode(this.data, res);
      }
    });

    this.treeHelisaService.refreshTreeObservable.subscribe((res: void) => {
      this.refreshTree();
    });

    this.treeHelisaService.refreshTreeWithPaginationObservable.subscribe((res: void) => {
      this.refreshTreeWithPagination();
    });
  }

  ngAfterViewInit(): void {
    this.treeHelisaService.nodeExpand.subscribe((res: boolean) => {
      if (res != null) {
        if (res) {
          this.tree.treeControl.expandAll();
        }
      }
    });

    this.treeHelisaService.nodeCollapse.subscribe((res: boolean) => {
      if (res !== null) {
        if (res) {
          this.tree.treeControl.collapseAll();
        }
      }
    });

    this.treeHelisaService.expandOneNodeObservable.subscribe((res: Node) => {
      if (res !== undefined) {
        this.treeControl.expand(res);
      }
    });

    this.treeHelisaService.collapseOneNodeObservable.subscribe((res: Node) => {
      if (res !== undefined) {
        this.treeControl.collapse(res);
      }
    });
  }

  //#region  ====== Events ===========
  onRedirect(node: Node): void {
    this.isSingleClick = true;
    setTimeout(() => {
      if (this.isSingleClick) {
        this.selectNode(node, node.id);

        // if(!!node && !node.children){
        if (!!node) {
          this.nodeSelected.emit(node.id);
          this.currentNode = node;
        }
      }
    }, 350);
  }

  onScroll(event: Event): void {
    const element: HTMLDivElement = event.target as HTMLDivElement;

    if (element.offsetHeight + element.scrollTop >= element.scrollHeight) {
      this.goNextPage();
    }
  }

  onEdit(node: Node): void {
    node.isEditable = true;
    this.isDisabled = true;
  }

  onAdd(node: Node): void {
    // si no tiene hijos instanciar el array
    if (!node.children) {
      node.children = [];
    }
    node.children.push({
      id: Math.random(),
      name: '',
      isSelected: false,
      parent: node,
      isEditable: true
    });
    if (node.children) {
      this.isDisabled = true;
      this.treeHelisaService.expandOneNode(node);
    }
    this.refreshTree();
  }

  onDelete(node: Node): void {
    // Remueve el nodo utilizando la libreria de lodash
    _.remove(node.parent.children, node);

    this.refreshTree();
    this.removed.emit(node.id);
  }

  onEdited(node: Node, value: string): void {
    node.name = value;

    if (node.id == null && node.name === '') {
      _.remove(node.parent.children, node);
      this.refreshTree();
    } else if (node.id && node.id != null && node.name.trim() !== '') {
      this.edited.emit(node);
      node.isEditable = false;
      this.selectNode(node, node.id);
    } else if (node.id == null && node.name.trim() !== '') {
      this.added.emit(node);
      node.isEditable = false;
    }
    this.isDisabled = false;
    this.refreshTree();
  }

  onCancel(node: Node, value: string): void {
    this.isDisabled = false;
    // Si no tiene id por ser un nuevo item, lo elimina
    if (node.id == null) {
      _.remove(node.parent.children, node);
      this.refreshTree();
    }

    node.isEditable = false;
  }

  onDblClick(node: Node): void {
    this.isSingleClick = false;
    this.dobleClick.emit(node.id);
  }

  @HostListener('document:keyup', ['$event'])
  onKeyDown(event: KeyboardEvent): void {
    switch (event.key) {
      case 'Delete':
        this.keypressDelete.emit(!!this.currentNode && this.currentNode.id ? this.currentNode.id : null);
        break;
      case 'Insert':
        this.keypressInsert.emit(!!this.currentNode && this.currentNode.id ? this.currentNode.id : null);
        break;
      case 'ArrowDown':
        this.moveDownIntoTree();
        break;
      case 'ArrowUp':
        this.moveUpIntoTree();
        break;
    }
  }

  //#endregion ======= Events ========

  //#region  ======== Metodos =============

  private moveUpIntoTree(): number {
    if (!!this.data) {
      // si aun no hay ningun node seleccionado selecciona el primero
      if (this.currentNode == null) {
        this.selectNode(this.data, this.data.children[0].id);
        this.currentNode = this.data.children[0];
        if (!!this.currentNode.children && this.currentNode.children.length > 0) {
          this.treeHelisaService.expandOneNode(this.currentNode);
        }
      } else {
        if (!!this.currentNode.parent && this.currentNode.id != null) {
          // obtiene el indice del nodo seleccionado actualmente
          const index: number = this.currentNode.parent.children.indexOf(this.currentNode);

          if (this.currentNode.parent.id == null && index === 0) {
            return 0;
          } else {
            // si tiene nodos al mismo nivel salta al nodo anterior
            if (index !== undefined && index === 0) {
              this.currentNode = this.currentNode.parent;
              this.selectNode(this.data, this.currentNode.id);
              if (!!this.currentNode.children && this.currentNode.children.length > 0) {
                this.treeHelisaService.expandOneNode(this.currentNode);
              }
            } else {
              // si no tiene nodos al mismo nivel salta al nodo padre
              this.currentNode = this.currentNode.parent.children[index - 1];
              this.selectNode(this.data, this.currentNode.id);
              if (!!this.currentNode.children && this.currentNode.children.length > 0) {
                this.treeHelisaService.expandOneNode(this.currentNode);
              }
            }
          }
        }
      }
    }
  }

  private moveDownIntoTree(): void {
    if (!!this.data) {
      if (this.currentNode == null) {
        this.selectNode(this.data, this.data.children[0].id);
        this.currentNode = this.data.children[0];
        if (!!this.currentNode.children && this.currentNode.children.length > 0) {
          this.treeHelisaService.expandOneNode(this.currentNode);
        }
      } else {
        if (!!this.currentNode) {
          // obtiene el indice del nodo seleccionado actualmente
          const index: number =
            !!this.currentNode && !!this.currentNode.parent ? this.currentNode.parent.children.indexOf(this.currentNode) : null;

          // si tiene childrens pasa al primer children
          if (!!this.currentNode.children && this.currentNode.children.length > 0) {
            this.currentNode = this.currentNode.children[0];
            this.selectNode(this.data, this.currentNode.id);
            if (!!this.currentNode.children && this.currentNode.children.length > 0) {
              this.treeHelisaService.expandOneNode(this.currentNode);
            }
          } else if (
            index !== undefined &&
            index === this.currentNode.parent.children.length - 1 &&
            this.currentNode.parent.parent != null &&
            this.currentNode.parent.parent.children != null &&
            this.currentNode.parent.parent.children.length > 0
          ) {
            const indexOfParent: number = this.currentNode.parent.parent.children.indexOf(this.currentNode.parent);
            this.currentNode =
              this.currentNode.parent.parent.children[indexOfParent + 1] === undefined
                ? this.currentNode
                : this.currentNode.parent.parent.children[indexOfParent + 1];

            this.selectNode(this.data, this.currentNode.id);
            if (!!this.currentNode.children && this.currentNode.children.length > 0) {
              this.treeHelisaService.expandOneNode(this.currentNode);
            }
          } else {
            // si no tiene nodos al mismo nivel salta al siguiente hacia abajo
            this.currentNode = this.currentNode.parent.children[index + 1];
            this.selectNode(this.data, this.currentNode.id);
            if (!!this.currentNode.children && this.currentNode.children.length > 0) {
              this.treeHelisaService.expandOneNode(this.currentNode);
            }
          }
        }
      }
    }
  }

  /**
   * Verifica si el nodo tiene hijos
   */
  hasChild(t: number, node: Node): boolean {
    return !!node.children && node.children.length > 0;
  }

  /**
   * Actualiza el arbol borrando toda la data , solo cuando no se utiliza paginacion
   */
  private refreshTree(): void {
    this.data = null;
    const datasourceData: Node[] = this.dataSource.data;
    this.dataSource.data = null;
    this.dataSource.data = datasourceData;
    this.treeControl.dataNodes = datasourceData;
  }

  /**
   * Actualiza el arbol cuando se utiliza la paginacion (Cuando no , utilice el metodo refreshTree())
   */
  private refreshTreeWithPagination(): void {
    const datasourceData: Node[] = this.dataSource.data;
    this.dataSource.data = null;
    this.dataSource.data = datasourceData;
    this.treeControl.dataNodes = datasourceData;
  }

  private goNextPage(): void {
    if (!this.treeHelisaConnect.isLastPage && !this.treeHelisaConnect.isUsed) {
      this.treeHelisaConnect.isUsed = true;
      this.rangeScrolled.emit({
        page: this.treeHelisaConnect.nextPage()
      });
    }
  }

  private receivePage(data: Node[]): void {
    if (!this.data) {
      this.data = { id: null, name: 'root', isSelected: false };
    }
    if (!this.data.children) {
      this.data.children = new Array<Node>();
      this.treeHelisaConnect = new TreeHelisaConnect<Node>();
    }
    this.data.children = this.data.children.concat(data);
    this.data.children.forEach((node: Node) => {
      this.fillParent(node, this.data);
    });

    this.data.children = this.reorderByOrderIndex(this.data.children);

    this.dataSource.data = this.data.children;
    this.treeControl.dataNodes = this.data.children;
    this.treeHelisaConnect.isLastPage = data.length === 0;
    this.treeHelisaConnect.isUsed = false;
  }

  /**
   * Llenan el campo parent de todos los nodos hijos
   */
  private fillParent(node: Node, parent: Node): void {
    node.parent = parent;
    if (node.children && node.children.length > 0) {
      node.children.forEach((item: Node) => {
        this.fillParent(item, node);
      });
    }
  }

  /**
   * coloca como true del isSelected del nodo que concuerde con el id
   */
  private selectNode(node: Node, id: number | string): Node {
    this.upSelectNode(node);
    if (!!node) {
      if (!!this.selectedNode) {
        const nodeSelected: Node = this.getNodeById(this.selectedNode);
        if (nodeSelected !== undefined) {
          nodeSelected.isSelected = false;
        }
      }
    }
    if (!!!node) {
      return null;
    }
    if (node.id !== undefined && node.id === id) {
      node.isSelected = true;
      this.expandAllParents(node);
      this.selectedNode = node.id;
      return node;
    } else if (node.children != null) {
      let i: number;
      let result: Node = null;
      for (i = 0; result == null && i < node.children.length; i++) {
        result = this.selectNode(node.children[i], id);
      }
      return result;
    }
    return null;
  }

  private expandAllParents(node: Node): void {
    if (!!node && !!node.parent) {
      this.treeHelisaService.expandOneNode(node.parent);
      this.expandAllParents(node.parent);
    }
  }

  /**
   * Elimina el isSelected de todos los nodos
   */
  private upSelectNode(node: Node): void {
    if (!!node && node.isSelected !== undefined) {
      node.isSelected = false;
      if (!!node.children) {
        for (const childrenNode of node.children) {
          this.upSelectNode(childrenNode);
        }
      }
    }
  }

  getClassNode(node: Node): string[] {
    const classNode: string[] = [];
    if (node.isSelected) {
      classNode.push('isSelected');
    }
    if (node.classNode) {
      classNode.push(node.classNode);
    }
    return classNode;
  }

  onEditMode(node: Node, editMode: boolean): void {
    this.getSelectedOptions(node).editMode = editMode;
  }

  onSelectOption(event: MatOptionSelectionChange, node: Node): void {
    node.isCheckedOption = event.source.selected;
    if (node.isCheckedOption) {
      this.checkedOptionNode.emit(node.id);
    } else {
      this.uncheckedOptionNode.emit(node.id);
    }
  }

  getSelectedOptions(
    node: Node
  ): {
    formControl: FormControl;
    editMode: boolean;
  } {
    if (this.selectedOptions.has(node.id)) {
      this.reloadSelectedOptions(node, this.selectedOptions.get(node.id).editMode);
    } else {
      this.reloadSelectedOptions(node, false);
    }
    return this.selectedOptions.get(node.id);
  }

  private reloadSelectedOptions(node: Node, editMode: boolean): void {
    const array: Array<string | number | null> = new Array<string | number | null>();
    node.options.forEach((option: Node) => {
      if (option.isCheckedOption) {
        array.push(option.id);
      }
    });
    const obj: {
      formControl: FormControl;
      editMode: boolean;
    } = { formControl: new FormControl(array), editMode };
    this.selectedOptions.set(node.id, obj);
  }

  /**
   * Retorna el primer Node que encuentre segun el id enviado o null si no hay ninguno
   * @param id  number | string
   * @returns Node o null si no hay un nodo con ese id
   */
  getNodeById(id: number | string): Node {
    const queue: Node[] = [...this.dataSource.data];
    while (queue.length > 0) {
      const curr: Node = queue.shift();
      if (curr.id === id) {
        return curr;
      } else {
        if (!!curr.children) {
          queue.push(...curr.children);
        }
      }
    }
    return null;
  }

  reorderByOrderIndex(node: Node[]): Node[] {
    if (!!node && node.length > 0) {
      try {
        node = _.orderBy(node, (x: Node) => x.orderIndex, ['asc']);
        node.forEach((element: Node) => {
          if (!!element.children && element != null) {
            element.children = this.reorderByOrderIndex(element.children);
          }
        });
        return node;
      } catch (error) {
        console.log(error);
      }
    }
  }

  //#endregion ====== Metodos ============
}
