import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable, Subject} from 'rxjs';
import {TotalTableHelisa} from './table-helisa.interface';
import {TableHelisaComponent} from './table-helisa.component';

export interface TableHelisaServiceInfo<T> {
  obj: T;
  table?: {} | TableHelisaComponent<T>;
}

@Injectable({
  providedIn: 'root'
})
export class TableHelisaService<T> {

  private emitChangeSource: Subject<TableHelisaServiceInfo<TotalTableHelisa>> = new Subject<TableHelisaServiceInfo<TotalTableHelisa>>();
  private emitNextPage: Subject<TableHelisaServiceInfo<T[]>> = new Subject<TableHelisaServiceInfo<T[]>>();

  totalReturn: Observable<TableHelisaServiceInfo<TotalTableHelisa>> = this.emitChangeSource.asObservable();
  nextPageReturn: Observable<TableHelisaServiceInfo<T[]>> = this.emitNextPage.asObservable();

  private emitVisibleButton$: Subject<boolean> = new Subject<boolean>();
  /**
   * Observable para saber si se debe mostrar o esconder el boton de add row
   */
  emitVisibleButton: Observable<boolean> = this.emitVisibleButton$.asObservable();

  setTotal(total: TotalTableHelisa, table?: TableHelisaComponent<TotalTableHelisa>): void {
    this.emitChangeSource.next({obj: total, table});
  }

  addPage(page: T[], table?: TableHelisaComponent<T>): void {
    this.emitNextPage.next({obj: page, table});
  }

  /**
   * para modificar el valor de si se muestra o no el boton de add row de la tabla
   * @param change indicar si se muestra o no el boton de add row de la tabla
   */
  changeVisibilityButton(change: boolean): void {
    this.emitVisibleButton$.next(change);
  }

}
