import { Injectable } from '@angular/core';
import { Observable, Subject, BehaviorSubject } from 'rxjs';
import {ColumnConfig, AddRowButton, ConfigRowStyles, TotalTableHelisa} from '../table-helisa/table-helisa.interface';
import { EventDependency} from './dependency-table-helisa.component';

export interface ConfigTable<T> {
  columns: Array<ColumnConfig>;
  isRemote: boolean;
  dataSource?: Array<T>;
  count?: number;
  order?: number;
  showTitle?: boolean;
  indexRowSelect?: number;
  isDragged?: boolean;
  addRowButton?: AddRowButton;
  configRowStylesFromColumn?: Array<ConfigRowStyles<T>>;
  isCellSelection?: boolean;
  addBookButton?: boolean;
}

@Injectable()
export class DependencyTableHelisaService<T> {


  tables: Subject<ConfigTable<T>[]> = new Subject();
  infoTables: Array<ConfigTable<T>> = new Array();

  private emitVisibilityButton$: Subject<EventDependency<boolean>> = new Subject<EventDependency<boolean>>();
  emitVisibilityButton: Observable<EventDependency<boolean>> = this.emitVisibilityButton$.asObservable();

  private emitVisibilityAllButtons$: Subject<boolean> = new Subject<boolean>();
  emitVisibilityAllButtons: Observable<boolean> = this.emitVisibilityAllButtons$.asObservable();

  private emitIsCellSelection$: Subject<EventDependency<boolean>> = new Subject<EventDependency<boolean>>();
  emitIsCellSelection: Observable<EventDependency<boolean>> = this.emitIsCellSelection$.asObservable();

  private emitChangeColumns$: Subject<EventDependency<ColumnConfig[]>> = new Subject<EventDependency<ColumnConfig[]>>();
  emitChangeColumns: Observable<EventDependency<ColumnConfig[]>> = this.emitChangeColumns$.asObservable();

  emitTotal: Subject<EventDependency<TotalTableHelisa>> = new Subject<EventDependency<TotalTableHelisa>>();
  emitNextPage: Subject<EventDependency<T[]>> = new Subject<EventDependency<T[]>>();

  constructor() { }

  /**
   * retorna un Observable<ConfigTable[]>
   */
  getTables(): Observable<ConfigTable<T>[]> {
    return this.tables;
  }

  /**
   * Actualiza las dependencias, agrendo la tabla que envian en el orden correspondiente o al final.
   * TambiÃ©n remueve las dependecias que hay apartir de la tabla segun se indique en el parametro.
   * @param configTable Objeto que contiene la configuraciÃ³n para la tabla.
   * @param withRemoveDependency boolean por defecto es false, si es 'true' indica que remueva las dependencias apartir de el.
   */
  updateDependency(configTable: ConfigTable<T>, withRemoveDependency: boolean = false): void {
    if (withRemoveDependency) {
      this.infoTables = this.infoTables.slice(0, !configTable.order ? 0 : configTable.order);
    }
    if (!configTable.order || configTable.order >= this.infoTables.length) {
      configTable.order = this.infoTables.length;
    }
    this.infoTables[configTable.order] = configTable;
    if (configTable.isRemote) {
      configTable.dataSource = null;
      if (configTable.count === null) {
        throw new Error('hace falta el count');
      }
    } else {
      if (configTable.dataSource === null) {
        throw new Error('hace falta el dataSource');
      }
      configTable.count = configTable.dataSource.length;
    }
    this.tables.next(this.infoTables);
  }

  /**
   * Emite un evento de total con la informaciÃ³n para la tabla correspondiente
   * @param event wrapper que contiene el indice de la tabla y la informaciÃ³n de la pagina
   */
  setTotal(event: EventDependency<TotalTableHelisa>): void {
    this.emitTotal.next(event);
  }

  /**
   * Emite un evento de agregar pagina con la pagina para la tabla correspondiente
   * @param event wrapper que contiene el indice de la tabla y la informaciÃ³n de la pagina
   */
  addPage(event: EventDependency<T[]>): void {
    this.emitNextPage.next(event);
  }

  selectIndexRow(config: ConfigTable<T>): void {
    if (this.infoTables[config.order]) {
      this.infoTables[config.order].indexRowSelect = config.indexRowSelect;
      this.tables.next(this.infoTables);
    }
  }


  /**
   * Muestra o esconde el boton una tabla en especifico
   * @param event para indicar el index de la tabla y en "data" true o false
   */
  changeVisibilityButton(event: EventDependency<boolean>): void {
    this.emitVisibilityButton$.next(event);
  }


  /**
   * Esconde los botones de todas las tablas
   * @param show indicar si se muestran o no todos los botones de las tablas
   */
  changeVisibilityAllButtons(show: boolean): void {
    this.emitVisibilityAllButtons$.next(show);
  }

  /**
   * Para habilitar el manejo de selecciÃ³n de celda
   * @param event para indicar el index de la tabla y en "data" true o false
   */
  changeisCellSelection(event: EventDependency<boolean>): void {
    this.emitIsCellSelection$.next(event);
  }

   /**
    * Para habilitar el cambio de columnas
    * @param event para indicar el index de la tabla y en "data" columnas
    */
  changeColumnsByTable( event: EventDependency<ColumnConfig[]>): void {
    this.emitChangeColumns$.next(event);
  }

}
