import { Injectable } from '@angular/core';
import { Subject, BehaviorSubject, Subscriber, Observable } from 'rxjs';
import { Node } from './node';


@Injectable({
    providedIn: 'root'
})
export class TreeHelisaService {

    // Observable string sources
    private emitNodeSelected: BehaviorSubject<number | string> = new BehaviorSubject<number | string>(1);
    // Observable string streams
    nodeSelected: Observable<string | number> = this.emitNodeSelected.asObservable();

    // Observable string sources
    private emitDataSource: BehaviorSubject<Node> = new BehaviorSubject<Node>(undefined);

    // Observable string streams
    dataSourceObservable: Observable<Node> = this.emitDataSource.asObservable();

    // Expand node observable
    private emitExpandAllNodes: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(null);
    nodeExpand: Observable<boolean> = this.emitExpandAllNodes.asObservable();

     // Collapse node observable
     private emitCollapseAllNodes: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(null);
     nodeCollapse: Observable<boolean> = this.emitCollapseAllNodes.asObservable();

    private emitRefreshTree: Subject<void> = new Subject<void>();
    refreshTreeObservable: Observable<void> = this.emitRefreshTree.asObservable();

    private emitRefreshTreeWithPagination: Subject<void> = new Subject<void>();
    refreshTreeWithPaginationObservable: Observable<void> = this.emitRefreshTreeWithPagination.asObservable();

    private emitExpandOneNode: Subject<Node> = new Subject<Node>();
    expandOneNodeObservable: Observable<Node> = this.emitExpandOneNode.asObservable();

    private emitCollapseOneNode: Subject<Node> = new Subject<Node>();
    collapseOneNodeObservable: Observable<Node> = this.emitCollapseOneNode.asObservable();

    // Service message commands
    changeNodeSelected(idResidentialArea: number | string): void {
        this.emitNodeSelected.next(idResidentialArea);
    }

    // Service message commands
    changeDataSource(data: Node): void {
        this.emitDataSource.next(data);
    }

    expandAllNodes(expand: boolean): void {
        this.emitExpandAllNodes.next(expand);
    }

    collapseAllNodes(collapse: boolean): void {
        this.emitCollapseAllNodes.next(collapse);
    }

    refreshTree(): void {
        this.emitRefreshTree.next();
    }


    refreshTreeWithPagination(): void {
        this.emitRefreshTreeWithPagination.next();
    }


    expandOneNode(node: Node): void {
    this.emitExpandOneNode.next(node);
    }


    collapseOneNode(node: Node): void {
        this.emitCollapseOneNode.next(node);
    }

    constructor() {

    }

}
